.team {
    margin-top: 70px;
    grid-template-columns: repeat(4, 1fr);
    // grid-template-rows: repeat(3, 1fr)
    .title {
        grid-column: 1/4;
        line-height: 40px;
        text-transform: uppercase;
        font-family: Raleway-ExtraBold;
        font-size: 24px;
        color: #333333; }
    @include r($xlm) {
        &::nth-child(4) {
            grid-row: 2;
            background-color: red; } } }
.team-tiles {
    grid-template-columns: repeat(3, 1fr);
    @include r($xlm) {
        grid-template-columns: repeat(2, 1fr); } }



.team {
    grid-template-columns: repeat(4, 1fr);
    @include r($xlm) {
        grid-template-columns: repeat(3, 1fr); }
    @include r($lg) {
        grid-template-columns: repeat(2, 1fr); }
    @include r($md) {
        grid-template-columns: repeat(1, 1fr);
        text-align: center;
        & * {
            margin: auto; } }
    .name {
        text-transform: uppercase;
        font-family: Raleway-SemiBold;
        font-size: 15px;
        color: #252525;
        margin-top: 15px; }
    .employee-position {
        line-height: 25px;
        font-family: Open Sans RegularItalic; }
    .photo {
        margin-right: $gutter;
        @include r($lg) {
            margin-right: 0; }
        img {
            max-height: 296px; } }

    .separator {}

    .title-block {
        grid-column: 1/4;
        @include r($xlm) {
            grid-column: 1/-1; } }

    .hiring-title {
        grid-column: 4/5;
        grid-row: 1;
        @include r($xlm) {
            grid-column: 1/-1;
            grid-row: 3;
            margin-top: 100px; }
        @include r($md) {
            grid-row: 6; } } }


.hiring-baner {
    // grid-column: 2
    @include r($xlm) {
        grid-row: 4;
        grid-column: 1; }
    @include r($md) {
        grid-row: 7; }
    .wrap {
        max-width: 300px;
        padding: 45px;
        text-align: center;
        line-height: 15px;
        & * {
            margin-bottom: 20px; }

        .btn-feed {
            position: relative;
            top: 28px;
            padding: 15px 27px; }

        .content {
            font-family: MyriadPro-Regular; } } }
