
.tabs {
    background-color: #f0f3f5;
    padding: 70px 0;
    .wrapper {
        grid-template-columns: 2fr 1fr;
        @include r($lg) {
            grid-template-columns: 1fr; } } }

.tabs-slider {
    $navSmWidth : 110px;
    // grid-template-areas:

    .slick-dots {
        font-size: 15.3px;
        grid-area: 1;
        li {
            float: left;
            position: absulute;
            margin-right: 2px;
            &.slick-active button {
                content: "";
                color: white;
                background-color: $accent;
                color: $accent;
                width: 166px;
                height: 49px;
                @include r($sm) {
                    width: $navSmWidth; }

                &::after {
                    content: ".";
                    background-image: url("../img/corner.png");
                    background-position: 0 4px;
                    background-repeat: no-repeat;
                    display: inline-block;
                    width: 31px;
                    height: 40px;
                    position: relative;
                    top: 40px; } }

            &.slick-active::before {
                color: white; }

            button {
                text-align: center;
                width: 166px;
                height: 49px;
                background-color: white;
                color: white;
                margin-right: 2px;
                @include r($sm) {
                    width: $navSmWidth; }
                &::hover {
                    background-color: #d5f9df; } }
            &::before {
                font-family: Raleway-ExtraBold;
                cursor: pointer;
                color: #333333;
                padding-top: 16px;
                width: 166px;
                height: 20px + 20px;
                position: absolute;
                text-align: center;
                text-transform: uppercase;
                @include r($sm) {
                    width: $navSmWidth; } } }

        li:nth-child(1)::before {
            content: "о нас"; }
        li:nth-child(2)::before {
            content: "контакты"; }
        li:nth-child(3)::before {
            content: "карта"; } }

    .slick-prev,.slick-next {
        display: none!important; } }


.slick-list {
    margin: 43px 0 0 0; }

.slick-slider {}
.about {
    font-family: Open Sans Regular;
    display: grid!important;
    grid-template-rows: 1fr 2fr 2fr 1fr;
    grid-template-columns: 1fr 2fr;
    line-height: 20px;
    @include r($md) {
        grid-template-rows: none; }
    @include r($sm) {
        grid-template-columns: 1fr; }
    .pic-about {
        display: grid;
        grid-template-columns: 2;
        width: 291px;
        height: 311px;
        grid-row: 1/5;
        padding-right: $gutter;
        @include r($sm) {
            padding: 0;
            width: 100%;
            grid-column: 1/-1; } }
    h2 {
        margin-top: -5px;
        font-size: 15.2px;
        font-family: Raleway-SemiBold;
        text-transform: none;
        color: #333333;
        line-height: 25px;
        @include r($sm) {
            margin-top: 40px!important; } }
    .subtitle {
        margin-top: -5px;
        @include r($md) {
            margin: 10px 0 15px; } }
    .achievements {
        margin-top: 8px;
        line-height: 25px;
        li i {
            margin-right: 12px; }
        @include r($md) {
            grid-column: 1/-1; } } }
.more {
    color: $accent;
    margin: auto 9px auto auto;
    i {
        font-size: 9px;
        margin-left: 9px; } }

.news-block {
    margin-left: 20px;
    @include r($md) {
        margin-top: 100px; }
    .news-title {
        // grid-column: 1/3
        color: #333333;
        font-family: Raleway-ExtraBold;
        font-size: 29px; }
    .news-item {
        grid-template-columns: 1fr 3fr;
        .title p {
            color: #333333;
            font-family: Raleway-Bold;
            font-size: 18px; }
        .news-pic {
            grid-row: 1/4;
            margin-right: 14px; }

        .date {
            font-family: Open Sans Regular;
            grid-row: 1;
            i {
                float: left;
                margin-right: 3px; } } }
    .all-news {
        margin: auto 9px auto auto;
        font-family: Open Sans RegularItalic;
        color: #888888;
        text-transform: none; } }
