.banner {
  display: grid;
  font-family: "Raleway-Regular";
  min-height: 78vh;
  font-size: 38.4px;
  color: white;
  background-size: cover!important;
  position: relative;
  align-content: center;
  &.margin-fix {
    @include r($lg) {
      margin-top: 117px; }
    @include rmin($lg) {
      .banner-content {
        padding-top: 151px; } } }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $dark;
    opacity: 0.42; }

  h1 {
    font-size: em(30);
    @include r($sm) {
      @include fs(20); }
    @include r($md) {
      @include fs(19); } }
  p {
    font-size: em(13);
    margin-top: 11px;
    height: 31px;
    @include r($sm) {
      @include fs(16); }
    @include r($md) {
      @include fs(15); } }
  .separator {
    width: 60px;
    margin: auto; } }

.banner-content {
  z-index: 1;
  text-align: center;
  padding: 0 $gutter 60px; }
