$f-color: #bcc0c4;
footer {
    background-color: $footer;
    color: $f-color;
    font-family: MyriadPro-Regular;
    .title {
        color: #ffffff;
        font-family: Open Sans light;
        font-weight: bold;
        margin-bottom: 33px;
        font-size: 15px;
        text-transform: uppercase; }

    .content {
        padding: 69px 0;
        grid-template-columns: repeat(4, 1fr);
        border-bottom: solid 1px #384653;
        @include r($md) {
            grid-template-columns: 1fr; }
        .spacer {
            @include r($md) {
                display: none; } } }

    .newsletter {
        max-width: 261px;
        @include r($md) {
            grid-column: 1/2; }
        .email-field {
            background-color: #49525a;
            border: solid 1px #404b55;
            padding: 9px;
            border-radius: 4px;
            height: 37px;
            margin-top: 50px;
            p {
                font-family: Open Sans RegularItalic;
                float: left; }

            .btn-mail {
                float: right;
                padding: 9px 15px;
                top: -9px;
                right: -10px;
                border-radius: 5px;
                position: relative;

                i {
                    color: white; } } } }

    .sitemap {
        padding-left: $gutter;
        line-height: 22px;
        @include r($md) {
            padding: $gutter 0; }
        ul li a {
            font-family: MyriadPro-Regular;
            color: $f-color;
            i {
                color: $f-color;
                float: left;
                margin-right: 7px; }
            p {
                text-transform: capitalize; } } }

    .contacts {
        address, p a {
            margin-bottom: 15px;
            color: $f-color;
            text-transform: none; }
        .mailto {
            margin-top: 15px; }

        .feed {
            margin-top: 25px;
            line-height: 30px;
            .btn {
                text-align: center;
                background-color: #565f66;
                margin-right: 5px;
                height: 30px;
                width: 30px;
                float: left;
                border-radius: 4px;
                // padding: 6px
                i {
                    color: white; } } } }

    .copyright {
        padding: 22px 0;
        text-transform: uppercase;
        font-family: Open Sans Regular; } }
