$duration: 1s;
.clients {
    .title {
        color: #333333; }
    padding: 130px 0 80px;
    text-align: center;
    & * {
        margin: auto; }
    .slide-item {
        width: 166px!important;
        height: 120px!important;
        display: flex!important;
        height: 178px;
        overflow: hidden;
        margin-top: 50px; }
    .slick-center img {
        width: 190px; }
    .slick-arrow {
        display: none!important; } }
